.App {
  display: flex;
  flex-direction: column;
  max-height: 100vh;
  background: black;
  color: white;
  font-family: "Trebuchet MS", sans-serif;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
}

.Header, .TabBar {
  line-height: 0;
  img {
    width: 100%
  }
}

.main {
  position: relative;
  overflow-y: auto;
}